@keyframes wobble {
    from {transform:rotate(-10deg);}
    to {transform:rotate(10deg);}
}

@keyframes reverse-wobble {
    from {
        transform:translateY(-4px);
        transform:rotate(10deg);
    }
    to {
        transform:translateY(4px);
        transform:rotate(-10deg);
    }
}

.profile-photo-wrapper {
    width: 8em;
    height: 8em;
}

.profile-photo-frame {
    position: relative;
    width: 100%;
    height: 100%;
    border: 4px solid #CCC;
    border-radius: 40% 60% 70% 30% / 40% 50% 50% 60%;
    overflow: hidden;
    animation: wobble 2s ease-in-out infinite alternate;
}

.profile-photo {
    position: absolute;
    top: -5%;
    left: -10%;
    width: 110%;
    height: 110%;
    border-radius: 50%;
    background-image: url(https://res.cloudinary.com/dzwu8mhc1/image/upload/v1598320589/20200821_160142_gndsbs.jpg);
    background-size: cover;
    animation: reverse-wobble 2s ease-in-out infinite alternate;
    
}